// 
// coverImageimageUrl

const STORAGE_KEY = 'anime_archive_data';

interface OldAnime {
  id: string;
  title: string;
  imageUrl?: string;
  coverImage?: string;
  year?: number;
  genres: string[];
  personalRating: number;
  notes?: string;
  watchedDate: string;
}

interface NewAnime {
  id: string;
  title: string;
  imageUrl?: string;
  year?: number;
  genres: string[];
  personalRating: number;
  notes?: string;
  watchedDate: string;
}

// 
export const migrateData = (): void => {
  try {
    // 
    const storedData = localStorage.getItem(STORAGE_KEY);
    if (!storedData) {
      console.log('');
      return;
    }

    // 
    const animeList = JSON.parse(storedData) as OldAnime[];
    console.log(':', animeList);

    // 
    const migratedList: NewAnime[] = animeList.map(anime => {
      const newAnime: NewAnime = {
        id: anime.id,
        title: anime.title,
        year: anime.year,
        genres: anime.genres,
        personalRating: anime.personalRating,
        notes: anime.notes,
        watchedDate: anime.watchedDate,
        // coverImageimageUrlimageUrl
        imageUrl: anime.coverImage || anime.imageUrl
      };
      return newAnime;
    });

    // 
    localStorage.setItem(STORAGE_KEY, JSON.stringify(migratedList));
    console.log(':', migratedList);
  } catch (error) {
    console.error(':', error);
  }
};

// 
migrateData();
