import React, { useState, useEffect } from 'react';
import { Box, IconButton, useTheme, CircularProgress } from '@mui/material';
import { ArrowBack, ArrowForward } from '@mui/icons-material';
import { keyframes } from '@mui/system';

// 
const fadeIn = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`;

const fadeOut = keyframes`
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
`;

interface ImageSlideshowProps {
  images: string[];
  autoPlayInterval?: number; // 
  height?: string | number;
  quality?: 'high' | 'medium' | 'low'; // 
  displayMode?: 'natural' | 'cover' | 'contain'; // 
}

const ImageSlideshow: React.FC<ImageSlideshowProps> = ({
  images,
  autoPlayInterval = 5000, // 5
  height = '60vh',
  quality = 'high', // 
  displayMode = 'natural', // 
}) => {
  const theme = useTheme();
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isAnimating, setIsAnimating] = useState(false);
  const [direction, setDirection] = useState<'next' | 'prev'>('next');
  const [loadedImages, setLoadedImages] = useState<{[key: string]: boolean}>({});
  const [preloadedImages, setPreloadedImages] = useState<string[]>([]);

  // 
  useEffect(() => {
    // 
    const preloadImages = () => {
      const imagesToLoad = [];
      // 
      imagesToLoad.push(images[currentIndex]);
      // 
      const nextIndex = (currentIndex + 1) % images.length;
      imagesToLoad.push(images[nextIndex]);
      // 
      const prevIndex = (currentIndex - 1 + images.length) % images.length;
      imagesToLoad.push(images[prevIndex]);

      // 
      const uniqueImages = [...new Set(imagesToLoad)];
      
      // 
      uniqueImages.forEach(src => {
        if (!preloadedImages.includes(src)) {
          const img = new Image();
          img.onload = () => {
            setLoadedImages(prev => ({ ...prev, [src]: true }));
            setPreloadedImages(prev => [...prev, src]);
          };
          img.src = src;
        }
      });
    };

    preloadImages();
  }, [currentIndex, images]);

  // 
  useEffect(() => {
    const timer = setInterval(() => {
      goToNext();
    }, autoPlayInterval);

    return () => {
      clearInterval(timer);
    };
  }, [currentIndex, autoPlayInterval]);

  // 
  const goToNext = () => {
    if (isAnimating) return;
    setDirection('next');
    setIsAnimating(true);
    setTimeout(() => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
      setIsAnimating(false);
    }, 500); // 
  };

  // 
  const goToPrev = () => {
    if (isAnimating) return;
    setDirection('prev');
    setIsAnimating(true);
    setTimeout(() => {
      setCurrentIndex((prevIndex) => (prevIndex - 1 + images.length) % images.length);
      setIsAnimating(false);
    }, 500); // 
  };

  return (
    <Box
      sx={{
        position: 'relative',
        width: '100%',
        height,
        overflow: 'hidden',
        borderRadius: theme.shape.borderRadius,
        mb: 4,
      }}
    >
      {/*  */}
      <Box
        sx={{
          position: 'absolute',
          width: '100%',
          height: '100%',
          backgroundImage: loadedImages[images[currentIndex]] ? `url(${images[currentIndex]})` : 'none',
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          filter: 'blur(20px)',
          opacity: 0.5,
          transform: 'scale(1.2)', // 
        }}
      />
      
      {/*  */}
      <Box
        sx={{
          position: 'absolute',
          width: '100%',
          height: '100%',
          background: 'linear-gradient(to bottom, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.4) 100%)',
          zIndex: 1,
        }}
      />

      {/*  */}
      <Box
        sx={{
          position: 'absolute',
          width: '100%',
          height: '100%',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          overflow: 'hidden',
          zIndex: 2,
        }}
      >
        {/*  */}
        {!loadedImages[images[currentIndex]] && (
          <Box
            sx={{
              position: 'absolute',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              width: '100%',
              height: '100%',
              zIndex: 3,
              bgcolor: 'rgba(0, 0, 0, 0.3)',
            }}
          >
            <CircularProgress color="primary" />
          </Box>
        )}
        
        {/*  */}
        <Box
          sx={{
            position: 'relative',
            width: displayMode === 'cover' ? '100%' : 'auto',
            height: displayMode === 'cover' ? '100%' : 'auto',
            maxWidth: displayMode === 'contain' ? '100%' : (displayMode === 'natural' ? '85%' : '100%'),
            maxHeight: displayMode === 'contain' ? '100%' : (displayMode === 'natural' ? '85%' : '100%'),
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            boxShadow: displayMode === 'natural' ? '0 10px 30px rgba(0,0,0,0.3)' : 'none',
            borderRadius: displayMode === 'natural' ? '8px' : '0',
            overflow: 'hidden',
          }}
        >
          <img
            src={images[currentIndex]}
            alt={` ${currentIndex + 1}`}
            style={{
              width: '100%',
              height: '100%',
              objectFit: displayMode === 'natural' ? 'contain' : (displayMode === 'cover' ? 'cover' : 'contain'),
              objectPosition: 'center',
              animation: `${isAnimating ? fadeOut : fadeIn} 0.5s ease-in-out`,
              transition: 'all 0.5s ease-in-out',
            }}
            onLoad={() => setLoadedImages(prev => ({ ...prev, [images[currentIndex]]: true }))}
          />
        </Box>
      </Box>

      {/*  */}
      <Box
        sx={{
          position: 'absolute',
          bottom: 0,
          width: '100%',
          height: '30%',
          background: 'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0) 100%)',
        }}
      />

      {/*  -  */}
      <Box
        sx={{
          position: 'absolute',
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          width: '100%',
          height: '100%',
          padding: '0 20px',
          opacity: 0,
          transition: 'opacity 0.3s',
          '&:hover': {
            opacity: 1,
          },
        }}
      >
        <IconButton
          onClick={goToPrev}
          sx={{
            bgcolor: 'rgba(255, 255, 255, 0.3)',
            color: 'white',
            '&:hover': {
              bgcolor: 'rgba(255, 255, 255, 0.5)',
            },
          }}
        >
          <ArrowBack />
        </IconButton>
        <IconButton
          onClick={goToNext}
          sx={{
            bgcolor: 'rgba(255, 255, 255, 0.3)',
            color: 'white',
            '&:hover': {
              bgcolor: 'rgba(255, 255, 255, 0.5)',
            },
          }}
        >
          <ArrowForward />
        </IconButton>
      </Box>

      {/*  */}
      <Box
        sx={{
          position: 'absolute',
          bottom: 16,
          left: '50%',
          transform: 'translateX(-50%)',
          display: 'flex',
          gap: 1,
        }}
      >
        {images.map((_, index) => (
          <Box
            key={index}
            sx={{
              width: 8,
              height: 8,
              borderRadius: '50%',
              bgcolor: index === currentIndex ? 'primary.main' : 'rgba(255, 255, 255, 0.5)',
              cursor: 'pointer',
              transition: 'all 0.3s',
            }}
            onClick={() => setCurrentIndex(index)}
          />
        ))}
      </Box>
    </Box>
  );
};

export default ImageSlideshow;
